*{margin:0;padding:0}body{font-family:'Helvetica Neue', Helvetica, STHeiTi, sans-serif;font-size:14px;line-height:1.5;-webkit-user-select:none;color:#000;-webkit-text-size-adjust:none;-webkit-tap-highlight-color:rgba(255,255,255,0)}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:normal}table{border-spacing:0;border-collapse:collapse}fieldset,img{border:0}em,i{font-style:normal}li{list-style:none}input,button,textarea,select{font-family:inherit;font-size:inherit;font-weight:inherit;border:0;outline:0;background:none;-webkit-appearance:none}a,button,input,select,textarea,a:focus,button:focus,input:focus,select:focus,textarea:focus,[onclick]{outline:0;-webkit-tap-highlight-color:rgba(255,255,255,0)}a{text-decoration:none;color:#00a5e0;outline:0;-webkit-touch-callout:none}::-webkit-input-placeholder{color:#999}
html,body{
    width: 100%;
    height: 100%;
    overflow: hidden;
}
#stageWrap{
    width: 100%;
    height: 100%;
}
canvas{
    // width: 100%;
    // height: 100%;
    overflow: hidden;
    cursor: default;
}
canvas:focus-visible,#stageWrap:focus-visible {
    outline: none;
    background: none;
}


#stageWrap{
    height: 100%;
}
.roomItemList{
    width: 750px;
    height: 545px;
    padding-top: 22px;
    background-color: #D5F2DF;
    border-top-right-radius: 40px;
    border-top-left-radius: 40px;
    .scroll-wrap {
        &::-webkit-scrollbar {
            display: none;
        }
        padding-top: 20px;
        padding-left: 35px;
        overflow: hidden;
        overflow-y: scroll;
        height: 510px;
        .item {
            position: relative;
            width: 151px;
            height: 151px;
            border-radius: 10px;
            background-color: #fff;
            border:1px solid #b2b2b2;
            float: left;
            margin-right: 25px;
            margin-bottom: 25px;
            // display: flex;
            // align-items: center;
            // justify-content: center;
            // gap 25
            .image{
                width: 124px;
                height: 124px;
                margin-left: 13px;
                margin-top: 13px;
                background-position: center;
                background-repeat: no-repeat;
                background-size: contain;
            }
            &.selected::after{
                content: '';
                position: absolute;
                width: calc(100% + 20px);
                height: calc(100% + 20px);
                left: -10px;
                top: -10px;
                border-radius: 10px;
                border:3px solid #50CBBC;
                box-sizing: border-box;
                pointer-events: none;
            }
            &.locked::before {
                content: '';
                position: absolute;
                width:23px;
                height: 26px;
                bottom: 45px;
                right: 10px;
                background-image: url(data:image/png;base64,);
                background-position: center;
                background-repeat: no-repeat;
                background-size: contain;
            }
            .name{
                position: absolute;
                bottom: 0px;
                left: 0px;
                right: 0px;
                height: 36px;
                line-height: 36px;
                background: #b2b2b2;
                border-radius: 8px;
                border-top-left-radius: 0;
                border-top-right-radius: 0;
                .text {
                    padding-left: 3px;
                    padding-right: 3px;
                    text-align: center;
                    font-size: 20px;
                    color: white;
                    width: 100%;
                    height: 36px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
        }
        .tag {
            position: absolute;
            left: 0px;
            top: 0px;
            padding: 0 10px;
            height: 33.43px;
            background: linear-gradient(180deg, #50CBBC 0%, #9CFFF3 100%);
            border-radius:9px 0px;
            font-size: 20px;
            line-height: 34px;
            text-align: center;
            color: #68544D;
        }
        .nums {
            position: absolute;
            right: 0px;
            top: 0px;
            padding: 0 10px;
            height: 34px;
            background: linear-gradient(180deg, #FFD167 0%, #FFC238 100%);
            border-radius: 0px 9px;
            font-size: 20px;
            line-height: 34px;
            text-align: center;
            color: #68544D;
        }
        .uploadImageBtn,.modifyColor{
            position: absolute;
            bottom: 5px;
            left: 5px;
            right: 5px;
            height: 37px;
            background: #68544D;
            border-radius: 6px;
            .text {
                font-size: 20px;
                line-height: 34px;
                text-align: center;
            }
        }
    }
}